import tjCheckBox from './tjCheckBox';
import caigou from '@/static/mbcImg/guanjianci/caigou.png';
import tuijian from '@/static/mbcImg/guanjianci/tuijian.png';
import gongying from '@/static/mbcImg/guanjianci/gongying.png';
import searchClose from '@/static/mbcImg/common/searchClose.png';
import del from '@/static/mbcImg/my/del.png'
export default {
	name: 'KeyWords',
	data() {
		return {
			selected:[],
			searchClose:searchClose,
			caigou:caigou,
			gongying:gongying,
			tuijian:tuijian,
			// caigou:caigou,
			selectList: [],
			purcKeys:[],//采购数据
			instKeys:[],//供应数据
			del:del,
			hiden:2,
			num:0,
			edit:'',
			blurType: '1', // 代表默认失去焦点事件，2代表点击添加时失去焦点事件	
			hidens:2,
			nums:0,
			edits:'',
			// blurType: '1', // 代表默认失去焦点事件，2代表点击添加时失去焦点事件
		};
	},
	created() {
		this.Keyword();
	},
	mounted() {
		
	},
	components:{
		tjCheckBox
	},
	methods: {
		// 接受多选组件传来点击的数据
		change(data) {
		    this.selected = data;
			console.log(this.selected);
		},
		Keyword(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/user/760', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.purcKeys = response.data.content.purcKeys;
						this.instKeys = response.data.content.instKeys;
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		buss(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				if(this.instKeys.length==0&&this.purcKeys.length==0&&this.selected.length==0){
					var params = {
						//供应
						instKeys:[''],
						//采购
						purcKeys:[''],
						//推荐
						pushKeys:[''],
						userId:'760',
					};
				}else if(this.instKeys.length==0&&this.purcKeys.length==0){
					var params = {
						//供应
						instKeys:[''],
						//采购
						purcKeys:[''],
						//推荐
						pushKeys:this.selected,
						userId:'760',
					};
				}else if(this.instKeys.length==0&&this.selected.length==0){
					var params = {
						//供应
						instKeys:[''],
						//采购
						purcKeys:this.purcKeys,
						//推荐
						pushKeys:[''],
						userId:'760',
					};
				}else if(this.purcKeys.length==0&&this.selected.length==0){
					var params = {
						//供应
						instKeys:this.instKeys,
						//采购
						purcKeys:[''],
						//推荐
						pushKeys:[''],
						userId:'760',
					};
				}else if(this.instKeys.length==0){
					var params = {
						//供应
						instKeys:[''],
						//采购
						purcKeys:this.purcKeys,
						//推荐
						pushKeys:this.selected,
						userId:'760',
					};
				}else if(this.purcKeys.length==0){
					var params = {
						//供应
						instKeys:this.instKeys,
						//采购
						purcKeys:[''],
						//推荐
						pushKeys:this.selected,
						userId:'760',
					};
				}else if(this.selected.length==0){
					var params = {
						//供应
						instKeys:this.instKeys,
						//采购
						purcKeys:this.purcKeys,
						//推荐
						pushKeys:[''],
						userId:'760',
					};
				}else{
					var params = {
						//供应
						instKeys:this.instKeys,
						//采购
						purcKeys:this.purcKeys,
						//推荐
						pushKeys:this.selected,
						userId:'760',
					};
					
				}
				 // 请求总数居时 参数为空
				uni.showLoading({
					// 展示loading
					title: '加载中'
				});
				console.log(params);
				uni.request({
					url: this.api2 + '/rest-rp/userKeys/setKeys', //接口地址。
					data: params,
					method: 'POST',
					header: {
						Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
					},
					success: response => {
						uni.hideLoading();
						console.log(response.data);
						// this.lists = response.data.content.instKeys
						this.getmy();
						uni.navigateBack({});
					},
					fail: error => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		getmy() {
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/user/760', //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						// console.log(response.data);
						this.list = response.data.content
						this.$store.commit('setMy', this.list); // 更新vuex
						// this.$store.commit('setMation', this.List); // 更新vuex
						
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		//跳过
		pass(){
			console.log("跳过此阶段");
		},
		dele(e){
			console.log(e)
			this.purcKeys.splice(e,1)
		},
		
		add(){
			this.hiden = '1';
			this.num = '1';
		},
		sub(){
			if(this.edit === ''){ // 失脚时没有数据
				this.hiden = 2
			}else{ // 失焦时存在数据
				this.hiden = 2;
				console.log('创建新标签');
				this.purcKeys.push(this.edit);
				console.log(this.purcKeys)
				this.edit = '';
			}
		},
		delet(e){
			console.log(e)
			this.instKeys.splice(e,1)
		},
		adds(){
			this.hidens = '1';
			this.nums = '1';
			console.log('创建新标签');
		},
		subs(){
			if(this.edits === ''){ // 失脚时没有数据
				this.hidens = 2
			}else{ // 失焦时存在数据
				this.hidens = 2
				this.instKeys.push(this.edits);
				console.log(this.instKeys)
				this.edits = '';
			}
		}
	}
};
